import React, { useState } from "react";
import "./Contact.css";
import NavBar from "./NavBar.js";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/core/styles";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import Button from "@material-ui/core/Button";
import Container from "@material-ui/core/Container";
import Image from "react-bootstrap/Image";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
    },
  },
}));

function Contact() {
  const [value, setValue] = useState();
  const classes = useStyles();
  return (
    <Container maxWidth="xl" disableGutters>
      <div className="contact">
        <NavBar />
        <div className="googleMap-div">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6497465.095965724!2d-104.42800488053922!3d37.3285294764158!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x808fb9fe5f285e3d%3A0x8b5109a227086f55!2sCalifornia%2C%20USA!5e0!3m2!1sen!2seg!4v1605194457090!5m2!1sen!2seg"
            frameborder="0"
            allowfullscreen=""
            aria-hidden="false"
            tabindex="0"
            className="googleMap-div"
          ></iframe>
        </div>
        <div className="contact-us-floating-application-form">
          <h3 className="secondRow2-title">
            Please fill in the following inputs
          </h3>

          <form
            className={classes.root}
            noValidate
            autoComplete="off"
            className="secondRow2-form"
          >
            <Row>
              <Col md={4} xs={4} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="First Name"
                  type="text"
                  required
                  className="secondRow2-cols-input"
                />
              </Col>
              <Col md={4} xs={4} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="Last Name"
                  type="text"
                  required
                  className="secondRow2-cols-input"
                />
              </Col>

              <Col md={4} xs={4} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="Email address"
                  type="email"
                  required
                  className="secondRow2-cols-input"
                />
              </Col>
            </Row>
            <Row className="contact-content-row">
              <Col md={4} xs={4} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="Organization"
                  type="text"
                  required
                  className="secondRow2-cols-input"
                />
              </Col>
              <Col md={4} xs={4} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="Job Title"
                  type="text"
                  required
                  className="secondRow2-cols-input"
                />
              </Col>
              <Col md={4} xs={4} className="secondRow2-cols">
                <PhoneInput
                  international
                  defaultCountry="EG"
                  value={value}
                  onChange={setValue}
                  className="secondRow2-cols-input"
                  placeholder="Phone number"
                />
              </Col>
            </Row>
            <Row>
              <Col md={12} xs={12} className="secondRow2-cols">
                <TextField
                  id="standard-basic"
                  label="How can we help?"
                  multiline
                  rows={3}
                  rowsMax={6}
                  required
                  className="secondRow2-cols-input"
                />
              </Col>
            </Row>
            <Row>
              <Col
                md={12}
                xs={12}
                className="secondRow2-cols secondRow2-cols-submit-button"
              >
                <Button variant="contained" className="about-us-fp-button">
                  Submit
                </Button>
              </Col>
            </Row>
          </form>
          <Row className="conact-us-in-person">
            <Col md={4} xs={4}>
              <span className="conact-us-in-person-span">Adress</span>
              <h6 className="conact-us-in-person-h6">
                1617 Alfred Drive, Calfornia
              </h6>
            </Col>
            <Col md={4} xs={4}>
              <span className="conact-us-in-person-span">Phone Number</span>
              <h6 className="conact-us-in-person-h6">718-237-3682</h6>
            </Col>
            <Col md={4} xs={5}>
              <span className="conact-us-in-person-span">Mail ID</span>
              <h6 className="conact-us-in-person-h6">itpeaks@gmail.com</h6>
            </Col>
          </Row>
        </div>
      </div>
    </Container>
  );
}

export default Contact;
