import React from "react";
import NavBar from "./NavBar.js";
import "./ServicesPage.css";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Link } from "react-router-dom";
import { faSketch } from "@fortawesome/free-brands-svg-icons";
import "animate.css/animate.min.css";
import ScrollAnimation from "react-animate-on-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBrain,
  faPencilRuler,
  faChess,
} from "@fortawesome/free-solid-svg-icons";
import Button from "@material-ui/core/Button";
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';
import MobileFriendlyIcon from '@material-ui/icons/MobileFriendly';
import LanguageIcon from '@material-ui/icons/Language';
import SettingsApplicationsIcon from '@material-ui/icons/SettingsApplications';
function ServicesPage() {
  return (
    <div className="services-page">
      <NavBar />
      <div className="services-page-content-div">
        <Row className="services-fp-mainRow">
          <Col md={6} xs={12} className="services-fp-firstPart">
            <div className="services-fp-firstPart-content-div">
              <h1 className="services-fp-h1-title">
                Services <br /> We
                <br /> Offer
              </h1>
              <p className="services-fp-para-text">
                Lorem ipsum dolor sit amet,
                <br /> consectetur adipisicing elit. Consectetur voluptas
                impedit,
                <br /> ratione velit esse suscipit? Eveniet reprehenderit
                facilis officia,
                <br /> consequuntur rerum exercitationem
              </p>
            </div>
            <Button variant="contained" className="services-fp-button">
              <Link to="/contact" className="tele-link-nav">
                Lets's Work Together
              </Link>
            </Button>
          </Col>
        </Row>
        <Row className="secondRow3">
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeInLeft"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card21">
                <EmojiObjectsIcon /*icon={faPencilRuler}*/ className="index-icon" />
                <a href="#web-app-developing" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle21">
                   Branding
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeIn"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card22">
                <MobileFriendlyIcon /*icon={faSketch}*/ className="index-icon" />
                <a href="#web-app-designing" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle22">
                    Mobile Development
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeIn"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card23">
                <LanguageIcon /*icon={faBrain}*/ className="index-icon" />
                <a href="#ui-ux" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle23">
                    Web Development
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
          <Col className="services-index-cols" md={3} xs={3}>
            <ScrollAnimation
              animateIn="animate__fadeInRight"
              animateOut="animate__fadeOut"
              duration={1}
            >
              <div className="services-card2" id="services-card24">
                <SettingsApplicationsIcon
                  /*icon={faChess}*/
                  className="index-icon"
                />
                <a href="#professional-consulting" className="index-link">
                  <h5 className="cardTitle2" id="cardTitle24">
                    System Integration
                  </h5>
                </a>
              </div>
            </ScrollAnimation>
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default ServicesPage;
